
.images {

  border-radius: 5px;
  height: 250px;
  margin-left: 75px;
  margin-right: 10px;
  margin-bottom: 2rem;
  position: relative;
  display: block;
  width: 250px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  float: left;
  padding: 20px;
 
}

.images > .img-container {
  height: 100%;
  overflow: hidden;

}
.images > .img-container > img {
  width: 100%
}





